.project img {
  max-width: 400px;
  width: 100%;
  margin: 20px;
  border-radius: 20px;
  align-self: center;
  object-fit: contain;
  border-color: transparent;
}

.project {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  margin: 20px;
  background-color: #e0e1dd;
  border-radius: 20px;
  transition: scale 0.5s ease;
  max-width: 400px;
  height: 400px;
}

.project h2 {
  line-height: 0;
}

/* on hover, it expands a little bit */
.project:hover {
  scale: 1.05;
  transition: scale 0.5s ease;
}

.projects {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
}

.projects a {
  color: #463f3a;
  text-decoration: none;
}

@media screen and (max-width: 800px) {
  .projects {
    flex-direction: column;
    width: auto;
  }
}

.projects-block {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

