.whoami {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.whoami h1 {
  color: #f02d3a;
}

.whoami p {
  font-size: 1.2em;
  font-weight: 300;
  line-height: 1.5;
  margin: 0;
  color: #463f3a;
  font-weight: 400;
}

.main-block {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 50px;
  background-color: #e0e1dd;
  color: white;
  width: 100%;
}

/* when the screen size is small it should be a column direction */
@media screen and (max-width: 800px) {
  .main-block {
    flex-direction: column;
    width: auto;
  }
}

.left-block .right-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.avatar {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  margin: 20px;
  /* maintain original aspect ratio */
  object-fit: cover;
}

.description {
  max-width: 500px;
}

.social {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 20px;
}

.social a {
  color: #463f3a;
  text-decoration: none;
  margin: 15px;
  font-size: 1.2em;
}
